import React, { Component } from "react";
import "./PublicationCard.css";
import { Fade, Flip } from "react-awesome-reveal";

class PublicationCard extends Component {
  render() {
    const publication = this.props.publication;
    const theme = this.props.theme;
    return (
      <div className="publication-card">
        <Flip direction="left" duration={500}>
          <div className="card-img">
            <img
              style={{
                maxWidth: "100%",
                maxHeight: "100%",
                transform: "scale(50%, 50%)",
                borderRadius: "50%",
                border: `2px solid ${theme.headerColor}`,
              }}
              src={require(`../../assests/images/${publication.logo_path}`)}
              alt={publication.alt_name}
            />
          </div>
        </Flip>
        <Fade direction="right" duration={.01} distance="40px">
          <div className="card-body">
            <div
              className="body-header"
              style={{ backgroundColor: theme.headerColor }}
            >
              <div className="body-header-title">
                <h2 className="card-title" style={{ color: theme.text }}>
                  {publication.title}
                </h2>
                <h3 className="card-subtitle" style={{ color: theme.text }}>
                  {publication.subtitle}
                </h3>
              </div>
              <div className="body-header-duration">
                <h3 className="duration" style={{ color: theme.text }}>
                  {publication.duration}
                </h3>
              </div>
            </div>
            <div classname="body-content">
              {publication.descriptions.map((sentence) => {
                return (
                  <p className="content-list" style={{ color: theme.text }}>
                    {sentence}
                  </p>
                );
              })}
              <a
                href={publication.website_link}
                target="_blank"
                rel="noopener noreferrer"
              >
                <div
                  className="visit-btn"
                  style={{ backgroundColor: theme.headerColor }}
                >
                  <p className="btn" style={{ color: theme.text }}>
                    View Paper
                  </p>
                </div>
              </a>
            </div>
          </div>
        </Fade>
      </div>
    );
  }
}

export default PublicationCard;
