import React, { Component } from "react";
import "./Publications.css";
import PublicationCard from "../../components/publicationCard/PublicationCard.js";
import { Fade } from "react-awesome-reveal";
import { connect } from 'react-redux';

class Publications extends Component {
  render() {
    const theme = this.props.theme;
    const { publications } = this.props.portfolio;
    return (
      <div className="main" id="publication">
        <div className="publications-header-div">
          <Fade direction="bottom" duration={2000} distance="20px">
            <h1 className="publications-header" style={{ color: theme.text }}>
              Publications
            </h1>
          </Fade>
        </div>
        <div className="publications-body-div">
          {publications.publications.map((publication, index) => {
            return <PublicationCard key={index} publication={publication} theme={theme} />;
          })}
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
	return {
		portfolio: state.portfolio
	};
};
export default connect(mapStateToProps)(Publications);
